//
// Vertical Navbar alignment options
//
// Display the navbar fixed to the top or bottom of the page.

// Fix the left/right navbars when screen real estate supports it
.navbar-fixed-left,
.navbar-fixed-right {

  width: @navbar-width;
  margin-bottom: 0; // override .navbar defaults

  position: fixed;
  top: 0;
  bottom: 0;
  z-index: @zindex-navbar-fixed;
  .translate3d(0, 0, 0);

  // Layout like navbar-fixed-top below breakpoint
  @media (max-width: @grid-float-breakpoint-max) {
    left: 0;
    right: 0;
    bottom: auto;
    width: auto;
  }

  // Undo the rounded corners
  @media (min-width: @grid-float-breakpoint) {
    border-radius: 0;
  }
}
.navbar-fixed-left {
  left: 0;
  border-width: 0 0 0 1px;
}
.navbar-fixed-right {
  right: 0;
  border-width: 0 1px 0 0;
}


// Brand/project name

.navbar-brand {

  @media (min-width: @grid-float-breakpoint) {
    .navbar-fixed-left &,
    .navbar-fixed-right & {
      float: none;
      display: inline-block;
    }
  }
