    //   }
    // }

    // &.navbar-right:last-child {
    //   margin-right: -@navbar-padding-horizontal;
    // }
  }

  // Dropdowns fly out to the side
  @media (min-width: @grid-float-breakpoint) {
    .navbar-fixed-left & {
      .dropdown-menu {
        top: 0;
        left: 100%;
      }
    }
    .navbar-fixed-right & {
      .dropdown-menu {
        top: 0;
        left: -100%;
      }
    }
  }
}


